/* Base styles */
:root {
    --primary-color: #1a1a1a;
    --secondary-color: #333;
    --accent-color: #ffd700;
    --text-color: #ffffff;
    --transition-speed: 0.3s;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Arial', sans-serif;
    background-color: var(--primary-color);
    color: var(--text-color);
    overflow-x: hidden;
}

.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

/* Loading Overlay */
.loading-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--primary-color);
    z-index: 9999;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: opacity var(--transition-speed);
}

.loading-overlay.fade-out {
    opacity: 0;
    pointer-events: none;
}

.loading-content {
    width: 50px;
    height: 50px;
    border: 3px solid var(--accent-color);
    border-radius: 50%;
    border-top-color: transparent;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

/* Mobile Header */
.mobile-header {
    background-color: var(--secondary-color);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
}

/* Hamburger Menu */
.hamburger-menu,
.hamburger-menu-mobile {
    background: none;
    border: none;
    cursor: pointer;
    padding: 10px;
    z-index: 1000;
}

.hamburger-menu {
    position: fixed;
    top: 20px;
    left: 20px;
}

.hamburger-icon {
    width: 30px;
    height: 20px;
    position: relative;
}

.hamburger-icon span {
    display: block;
    position: absolute;
    height: 2px;
    width: 100%;
    background: var(--text-color);
    transition: var(--transition-speed);
}

.hamburger-icon span:nth-child(1) { top: 0; }
.hamburger-icon span:nth-child(2) { top: 9px; }
.hamburger-icon span:nth-child(3) { top: 18px; }

/* Menu Overlay */
.menu-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.9);
    z-index: 999;
    opacity: 0;
    visibility: hidden;
    transition: opacity var(--transition-speed), visibility var(--transition-speed);
}

.menu-overlay.active {
    opacity: 1;
    visibility: visible;
}

.menu-content {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.menu-titles {
    text-align: center;
}

.menu-section {
    margin: 20px 0;
}

.menu-header {
    font-size: 2rem;
    margin-bottom: 10px;
    color: var(--accent-color);
}

.menu-subtitle {
    display: block;
    color: var(--text-color);
    text-decoration: none;
    font-size: 1.2rem;
    margin: 5px 0;
    transition: color var(--transition-speed);
}

.menu-subtitle:hover {
    color: var(--accent-color);
}

/* Main Content */
.main-page-content {
    padding: 20px;
    margin-top: 60px;
}

.title {
    text-align: center;
    font-size: 3rem;
    margin: 40px 0;
}

.zero {
    color: var(--accent-color);
}

/* Showcase */
.showcase {
    max-width: 1200px;
    margin: 0 auto;
}

.showcase-filters {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-bottom: 30px;
}

.filter-btn {
    background: none;
    border: none;
    color: var(--text-color);
    cursor: pointer;
    font-size: 1rem;
    padding: 5px 15px;
    transition: color var(--transition-speed);
}

.filter-btn:hover,
.filter-btn.active {
    color: var(--accent-color);
}

.showcase-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
    padding: 20px;
}

/* Top Right Icon */
.top-right-icon {
    position: fixed;
    top: 20px;
    right: 20px;
    z-index: 100;
}

.ale-icon {
    transition: transform var(--transition-speed);
}

.ale-icon:hover {
    transform: rotate(360deg);
}

/* Responsive Design */
@media (max-width: 768px) {
    .title {
        font-size: 2rem;
        margin: 20px 0;
    }

    .showcase-filters {
        flex-wrap: wrap;
    }

    .filter-btn {
        font-size: 0.9rem;
    }
}

/* Utility Classes */
.d-none {
    display: none !important;
}

.d-lg-none {
    display: none !important;
}

.d-lg-block {
    display: block !important;
}

@media (min-width: 992px) {
    .d-lg-none {
        display: none !important;
    }
    
    .d-lg-block {
        display: block !important;
    }
}
